.player {
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.time-control {
  width: 50%;
  display: flex;
  align-items: center;
  input {
    width: 100%;
    // -webkit-appearance: none;
    // background: transparent;
    cursor: pointer;
  }

  p {
    padding: 1rem;
  }
}
.play-control {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;

  width: 40%;
  svg {
    cursor: pointer;
  }
}
@media screen and (max-width: 770px) {
  .time-control {
    width: 90%;
  }
  .play-control {
    width: 80%;
  }
}
